<template>
  <login-bg>
    <div class="body">
      <h3 class="form-title">
        用户登录
        <div class="line"></div>
      </h3>
      <el-form :model="formFields"
        :rules="rules"
        ref="form">
        <el-form-item prop="accountNo">
          <el-input placeholder="请输入账号"
            v-model.trim="formFields.accountNo"
            @input="onInput">
            <span slot="prefix"
              class="prefix">
              <i class="iconfont bj-account"></i>
            </span>
          </el-input>
        </el-form-item>
        <el-form-item prop="passwords">
          <el-input placeholder="请输入密码"
            v-model.trim="formFields.passwords"
            type="password"
            autocomplete="new-password">
            <span slot="prefix"
              class="prefix">
              <i class="iconfont bj-pwd"></i>
            </span>
          </el-input>
        </el-form-item>
        <el-form-item prop="orgName">
          <el-input :value="formFields.orgName"
            @click.native="getOrg(formFields.accountNo)"
            readonly>
            <span slot="prefix"
              class="prefix">
              <i class="iconfont bj-jigou"></i>
            </span>
          </el-input>
        </el-form-item>
        <el-form-item class="about-pwd">
          <el-checkbox v-model="isRemenber">记住密码</el-checkbox>
          <a class="link fr"
            @click="$router.push({name: 'ForgetPwd'})">忘记密码</a>
        </el-form-item>
        <a class="a-btn"
          @click="login"
          :class="{'loading': loading}">
          <i class="el-icon-loading"
            v-if='loading'></i>
          {{loading ? '登录中...' : '登 录'}}
        </a>
      </el-form>
    </div>
  </login-bg>
</template>

<script>
import LoginBg from './LoginBg.vue'
import { mapGetters } from 'vuex'
import { decryptPwd, throttle } from '@/lib/utils'
import { MessageBox } from 'element-ui'
export default {
  name: 'Login',
  components: { LoginBg },
  data() {
    return {
      formFields: {
        accountNo: '',
        passwords: '',
        orgGuid: '',
        orgName: ''
      },
      rules: {
        accountNo: [{ required: true, message: '请输入账号', trigger: 'blur' }],
        passwords: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 8, max: 16, message: '密码长度为8~16字符', trigger: 'blur' }
        ],
        orgName: { required: true, message: '没找到对应的机构' }
      },
      isRemenber: false,
      loading: false
    }
  },
  created() {
    this.initData()
  },

  mounted() {
    window.addEventListener('keyup', this.handleEnterKey)
  },

  destroyed() {
    window.removeEventListener('keyup', this.handleEnterKey)
  },

  methods: {
    initData() {
      this.isRemenber = this.b_isRemenber
      if (this.b_isRemenber) {
        let { accountNo, passwords } = this.o_userinfo
        this.formFields.passwords = decryptPwd(passwords)
        this.formFields.accountNo = accountNo
        this.getOrg(accountNo) // 取返回的机构名称, 有可能修改过机构名称
      }
    },

    login() {
      if (this.loading) return
      this.$refs.form.validate(valid => {
        if (!valid) return
        this.loading = true
        this.$store
          .dispatch('login', {
            params: this.formFields,
            isRemenber: this.isRemenber
          })
          .then(_ => {
            this.loading = false
          })
          .catch(e => (this.loading = false))
      })
    },

    onInput(val) {
      throttle({ method: this.getOrg, params: val.trim() }) // 节流
    },

    // 获取机构
    getOrg(val) {
      if (val === '') {
        this.formFields.orgGuid = ''
        this.formFields.orgName = ''
        this.$nextTick(() => this.$refs.form.clearValidate('orgName'))
        return
      }
      this.$post('/login/getOrgByAccountNo', { accountNo: val })
        .then(({ data }) => {
          let { oname, sysGuid } = data.sysOrgDTO
          this.formFields.orgGuid = sysGuid
          this.formFields.orgName = oname
        })
        .catch(err => {
          this.formFields.orgGuid = ''
          this.formFields.orgName = ''
        })
    },

    // 回车提交登录
    handleEnterKey(e) {
      e.keyCode === 13 && this.login()
    }
  },

  computed: {
    ...mapGetters(['o_userinfo', 'b_isRemenber'])
  }
}
</script>

<style lang="less" scoped>
@import './style.less';
.body {
  /deep/ .el-form .el-form-item .el-input__inner {
    padding-left: 60px;
  }
  .prefix {
    padding-left: 24px;
  }
  .iconfont {
    font-size: 20px;
    color: @color-green;
  }
}
.about-pwd {
  margin-top: -24px;
  padding-left: 5px;
}
</style>

<style>
.el-select-dropdown.no-border {
  border: 0 none;
}
</style>



